@import '../../sass/pages/variables';
.header {
    width: 100%;
    height: 75px;
    background-color: $navColor;
    position: fixed;
    z-index: 30;
    right: 0;
    top: 0;
  
    &__header{
        margin-top: 7px;
        margin-right: 10px;
        height: 60px;
        color: $headerColor;
        font-size: 42px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}