@import '../../sass/pages/variables';
.courses {
    a {
        text-decoration: none;
        color: rgba(0, 0, 0, 0.918);
        &:hover {
            cursor: pointer;
            color: $aHover;
        }
    }
    margin: 75px 25px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &__item {
        position: relative;
        top: 85px;
        display: flex;
        flex-direction: row;
        justify-content: start;
        align-items: center;
        margin-bottom: 5px;
        padding: 5px;
        animation: couAnim 0.5s;
        &:hover {
            border-radius: 5px;
            box-shadow: $shadow;

        }    
        &-img {
            margin-right: 30px;
            img {
                width: 80px;
                height: 80px;
                border-radius: 100%;
            }
        }
        &-text {
            font-size: 30px;
            font-weight: 700;
        }
    }

}
.header {
        height: 60px;
        color: $headerColor;
        font-size: 48px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
}

@keyframes couAnim {
    from {
        opacity: 0;
        margin-bottom: 50px;
    }

    to {
        opacity: 1;
        margin-bottom: 5px;
    }
}